/*
    attrvalarrayToSearch
 */
function findElem(arrayToSearch,attr,val){
    for (var i=0;i<arrayToSearch.length;i++){
        if(arrayToSearch[i][attr]==val){
            return i;
        }
    }
    return -1;
}
/*
    Array object key
    desc: true|false 
    example: array.sort(keysrt('updated_at', true))
 */
function keysrt(key,desc) {
    return function(a,b){
        if(desc){
            res = a[key] < b[key];
            if(res){
                return 1;
            }else{
                return -1;
            }
        }else{
            res = a[key] > b[key];
            if(res){
                return 1;
            }else{
                return -1;
            }
        }
    }
}